'use client';

import { useState } from 'react';
import { MoreHorizontal } from 'lucide-react';
import { Button } from '../../button';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '../../dropdown-menu';
import { toast } from 'sonner';
import { DataTableDeleteDialog } from '../delete-dialog';
import { DataTableEditDialog } from '../edit-dialog';

export interface DataTableRowActionsProps<TData> {
  row: TData;
  onEdit?: (row: TData) => Promise<void>;
  onDelete?: (row: TData) => Promise<void>;
  editForm?: React.ReactNode;
  editTitle?: string;
}

/**
 * 
 *
 * @description
 * 
 * - 
 * - 
 *
 * UI
 * 
 *
 * @example
 * ```tsx
 * <DataTableRowActions
 *   row={row}
 *   onEdit={async (row) => {
 *     // 
 *   }}
 *   onDelete={async (row) => {
 *     // 
 *   }}
 *   editForm={<EditForm row={row} onSubmit={handleSubmit} />}
 *   editTitle=""
 * />
 * ```
 */
export function DataTableRowActions<TData>({
  row,
  onEdit,
  onDelete,
  editForm,
  editTitle,
}: DataTableRowActionsProps<TData>) {
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [showEditDialog, setShowEditDialog] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const handleDelete = async (row: TData) => {
    try {
      setIsLoading(true);
      await onDelete?.(row);
      toast.success('', {
        description: '',
      });
      setShowDeleteDialog(false);
    } catch (error) {
      console.error('Delete failed:', error);
      toast.error('', {
        description:
          error instanceof Error ? error.message : '',
      });
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = async (row: TData) => {
    try {
      setIsLoading(true);
      await onEdit?.(row);
      toast.success('', {
        description: '',
      });
      setShowEditDialog(false);
    } catch (error) {
      console.error('Edit failed:', error);
      toast.error('', {
        description:
          error instanceof Error ? error.message : '',
      });
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <>
      <DropdownMenu>
        <DropdownMenuTrigger asChild>
          <Button
            variant="ghost"
            className="flex h-8 w-8 p-0 data-[state=open]:bg-muted"
            disabled={isLoading}
          >
            <MoreHorizontal className="h-4 w-4" />
            <span className="sr-only"></span>
          </Button>
        </DropdownMenuTrigger>
        <DropdownMenuContent align="end" className="w-[160px]">
          {onEdit && editForm && (
            <DropdownMenuItem
              onClick={() => setShowEditDialog(true)}
              disabled={isLoading}
            >
              
            </DropdownMenuItem>
          )}
          {onEdit && onDelete && <DropdownMenuSeparator />}
          {onDelete && (
            <DropdownMenuItem
              onClick={() => setShowDeleteDialog(true)}
              className="text-destructive focus:text-destructive"
              disabled={isLoading}
            >
              
            </DropdownMenuItem>
          )}
        </DropdownMenuContent>
      </DropdownMenu>

      {onDelete && (
        <DataTableDeleteDialog
          open={showDeleteDialog}
          onOpenChange={setShowDeleteDialog}
          onDelete={handleDelete}
          row={row}
        />
      )}

      {onEdit && editForm && (
        <DataTableEditDialog
          open={showEditDialog}
          onOpenChange={setShowEditDialog}
          onEdit={handleEdit}
          row={row}
          title={editTitle}
        >
          {editForm}
        </DataTableEditDialog>
      )}
    </>
  );
}
